import React, {useCallback, useState} from 'react';
import {ThemeProvider} from '@mui/material/styles';
import {CssBaseline, StyledEngineProvider} from '@mui/material';
import theme from './themes';
import Container from '@mui/material/Container';
import Header from './components/Header'
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import {styled} from '@mui/material/styles';
import Box from '@mui/material/Box';
import FileDropper from './components/FileDropper';
import JsonResultViewer from './components/JsonResultViewer';
import axios from 'axios';

const Item = styled(Paper)(({theme}) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
}));

function App() {
    const [json, setJson] = useState(undefined);
    return (
        <ThemeProvider theme={theme}>
            {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
            <CssBaseline/>
            <Header/>
            <Box sx={{flexGrow: 1, padding: 5, height: '80vh'}}>
                <Grid container spacing={10} sx={{height: '100%'}}>
                    <Grid item xs={6} sx={{height: '100%'}}>
                        <FileDropper filePassed={file => {
                            let formData = new FormData()
                            formData.append("file", file)
                            axios.post<FormData>("/api/entrypoint", formData).then(
                                (response: { data: any; }) => {
                                    console.log(response)
                                    setJson(response.data)
                                }
                            ).catch(function (error) {
                                setJson(undefined)
                                if (error.response) {
                                    console.log(error.response.data);
                                    console.log(error.response.status);
                                    console.log(error.response.headers);
                                } else if (error.request) {
                                    console.log(error.request);
                                } else {
                                    console.log('Error', error.message);
                                }
                            })
                        }}/>
                    </Grid>
                    <Grid item xs={6} sx={{height: '100%'}}>
                        <JsonResultViewer jsonData={json}/>
                    </Grid>
                </Grid>
            </Box>
        </ThemeProvider>
    );
}

export default App;
